import React from "react";
import { DiscussionEmbed } from "disqus-react";

interface IDisqusConfiguration {
  url: string;
  identifier: string;
  title: string;
}

export const ArticleComment: React.FC<IDisqusConfiguration> = ({
         url,
         identifier,
         title,
       }) => {
         return (
           <div className="blog-comments-section">
             <DiscussionEmbed
               shortname="codingpro-1"
               config={{
                 url: url,
                 identifier: identifier,
                 title: title,
               }}
             />
           </div>
         );
       };
