import React, { Fragment } from "react";
import articles from "./articles.json";
import { RouteComponentProps } from "react-router";
import { ArticleHeader } from "./common/ArticleHeader";

interface RootParams {
  type: string;
}
interface IProps extends RouteComponentProps<RootParams> {}

export const ArticlesHome: React.FC<IProps> = ({ match }) => {
  const pageSize = 10;
  const articlesMediaList = articles.Details;
  var content: any[] = [];
  if (match.params.type && match.params.type.toLowerCase() !== "home") {
    content = articlesMediaList
      .filter((x) => x.type.toLowerCase() === match.params.type.toLowerCase())
      .slice(0, pageSize);
  } else {
    content = articlesMediaList
      .sort((n1, n2) => {
        if (n1.createdDate > n2.createdDate) {
          return 1;
        }
        return 0;
      })
      .slice(0, pageSize);
  }

  return (
    <Fragment>
      <section className="blog-list px-3 py-5 p-md-5">
        {content.map((a) => (
          <div className="container single-col-max-width">
            <hr />
            <div className="item mb-1">
              <div className="media">
                <img
                  className="mr-3 img-fluid post-thumb-sm d-none d-md-flex"
                  src={`${process.env.PUBLIC_URL}/${a.image}`}
                  alt={`for ${a.image}`}
                />
                <div className="media-body">
                  <ArticleHeader
                    title={a.title}
                    type={a.type}
                    identifier={a.article}
                    published={a.createdDate}
                    time={a.readTime}
                  />
                  <div className="intro">
                    <span>
                      <a
                        href={`${process.env.PUBLIC_URL}/post/${a.type}/${a.identifier}`}
                      >
                        {a.description}
                      </a>
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </section>
    </Fragment>
  );
};
