import React, { Fragment } from "react";
import { MenuItem } from "../../nav/MenuItem";
import articles from "../articles.json";
import { v4 as uuid } from "uuid";

export const ArticlesNavigation = () => {
  const articlesMenuList = articles.ArticleMenu;
  var contents: any[] = [];
  articlesMenuList.forEach((content)=>{
    contents.push(<MenuItem menuLink={`articles/${content.toLowerCase()}`} text={content} />);
  })

    return (
      <Fragment>
        <header className="article-header">
          <nav className="navbar navbar-expand-lg">
            <div className="collapse navbar-collapse">
              <ul className="navbar-nav text-left">{contents}</ul>
              <form className="form-inline my-2 my-lg-0">
                <input
                  className="form-control mr-sm-2"
                  type="text"
                  placeholder="Search"
                  name="searchTerm"
                />
                <button className="btn btn-primary" type="submit">
                  Search
                </button>
              </form>
            </div>
          </nav>
        </header>
        <br className="no-br" />
      </Fragment>
    );
};
