import React, { Fragment } from "react";
import { ArticleHeader } from "./common/ArticleHeader";
import { Banner } from "./common/Banner";
import { CodeHighlighter } from "../../common/util/CodeHighlighter";
import { ArticleComment } from "./common/ArticleComment";
import { ParagraphHeader } from "./common/ParagraphHeader";
import { createMarkup } from "../../common/util/uti";
import { RouteComponentProps } from "react-router";
import { ArticleOL } from "./common/ArticleOL";

interface IContent {
  p_header?: string;
  p_content?: string;
  p_caption?: ICaption;
  code?: string;
  p_ol?: any[];
}
interface ICaption {
  id: string;
  src: string;
  href?: string;
  caption_text: string;
  caption_href?: string;
}
interface RootParams {
  type: string;
  name: string;
}
interface IProps extends RouteComponentProps<RootParams> {}

export const ArticlePost: React.FC<IProps> = ({ match }) => {
  const article = require(`./contents/${match.params.type}/${match.params.name}.json`);
  var contents: any[] = [];

  article.content.map((x: IContent) => {
    if (x.p_header) {
      contents.push(<ParagraphHeader text={x.p_header!} />);
    }
    if (x.p_caption) {
      contents.push(
        <Banner
          id={x.p_caption.id}
          src={`${process.env.PUBLIC_URL}/${x.p_caption.src}`}
          captionText={x.p_caption.caption_text}
          href={x.p_caption.href}
          captionHref={x.p_caption.caption_href}
        />
      );
    }
    if (x.p_content) {
      contents.push(<p dangerouslySetInnerHTML={createMarkup(x.p_content)} />);
    }
    if (x.code) {
      contents.push(<CodeHighlighter codeString={atob(x.code)} />);
    }
    if (x.p_ol) {
      contents.push(<ArticleOL items={x.p_ol} />);
    }
  });

  return (
    <Fragment>
      <article className="blog-post px-3 py-5 p-md-5">
        <div className="container single-col-max-width">
          {/* Post Header */}
          <ArticleHeader
            title={article.title}
            published={article.header.createdDate}
            time={article.header.time}
          />

          <div className="intro">
            <span>
              <i>{article.header.description}</i>
            </span>
          </div>

          {/* Post Body */}
          <div className="blog-post-body">{contents}</div>
          <ArticleComment
            url={window.location.href}
            identifier={article.title}
            title={article.title}
          />
        </div>
      </article>
    </Fragment>
  );
};
