import React from "react";
import { MenuItem } from "./MenuItem";

export const Navigation = () => {
  return (
    <div id="navigation" className="collapse navbar-collapse flex-column">
      <div className="profile-section pt-3 pt-lg-0">
        <img
          className="profile-image mb-3 rounded-circle mx-auto"
          src={`${process.env.PUBLIC_URL}/assets/images/profile.jpg`}
          alt="for profile"
        />

        <div className="bio mb-3">
          Engineer specialized in DevOps | SRE | AWS | Cloud automation | CI/CD
          | Scripting &amp; coding in Python | PowerShell | C# | NodeJs | 🏏 🥊 🇮🇳
        </div>
        {/* <!--//bio--> */}
        <ul className="social-list list-inline py-2 mx-auto">
          <li className="list-inline-item">
            <a
              href="https://twitter.com/manish_bhr"
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="fab fa-twitter fa-fw"></i>
            </a>
          </li>
          <li className="list-inline-item">
            <a
              href="https://www.linkedin.com/in/manish-kumar-choudhary/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="fab fa-linkedin-in fa-fw"></i>
            </a>
          </li>
          <li className="list-inline-item">
            <a
              href="https://github.com/manishki"
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="fab fa-github-alt fa-fw"></i>
            </a>
          </li>
          <li className="list-inline-item">
            <a
              href="https://stackoverflow.com/users/4523305/manish-kumar-choudhary"
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="fab fa-stack-overflow fa-fw"></i>
            </a>
          </li>
          <li className="list-inline-item">
            <a
              href="https://codepen.io/manishki"
              target="_blank"
              rel="noopener noreferrer"
            >
              <i className="fab fa-codepen fa-fw"></i>
            </a>
          </li>
        </ul>
        {/* <!--//social-list--> */}
        <hr />
      </div>
      {/* <!--//profile-section--> */}

      <ul className="navbar-nav flex-column text-left">
        <MenuItem
          menuLink="about"
          text="About me"
          iconClass="fas fa-user fa-fw mr-2"
        />
        {/* <MenuItem
          menuLink="portfolio"
          text="Portfolio"
          iconClass="fas fa-laptop-code fa-fw mr-2"
        /> */}
        {/* <MenuItem
          menuLink="services"
          text="Services &amp;
            Pricing"
          iconClass="fas fa-briefcase fa-fw mr-2"
        /> */}
        <MenuItem
          menuLink="resume"
          text="Resume"
          iconClass="fas fa-file-alt fa-fw mr-2"
        />
        <MenuItem
          menuLink="blog-home"
          text="Blog"
          iconClass="fas fa-blog fa-fw mr-2"
        />
        <MenuItem
          menuLink="contact"
          text="Contact"
          iconClass="fas fa-envelope-open-text fa-fw mr-2"
        />
        {/* <MenuItem menuLink="/#" text=""> */}
        <li
          className={
            window.location.pathname.includes(
              `${process.env.PUBLIC_URL}/project`
            ) ||
            window.location.pathname.includes(
              `${process.env.PUBLIC_URL}/blog-alt-home`
            ) ||
            window.location.pathname.includes(
              `${process.env.PUBLIC_URL}/blog-post`
            )
              ? "nav-item dropdown active"
              : "nav-item dropdown"
          }
        >
          <a
            className="nav-link dropdown-toggle"
            href="/#"
            id="navbarDropdown"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            <i className="fas fa-cogs fa-fw mr-2"></i>More Pages
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
            <a
              className={
                window.location.pathname.includes(
                  `${process.env.PUBLIC_URL}/project`
                )
                  ? "dropdown-item active"
                  : "dropdown-item"
              }
              href={`${process.env.PUBLIC_URL}/project`}
            >
              Project Page
            </a>
            {/* <a
              className="dropdown-item"
              href={`${process.env.PUBLIC_URL}/blog-home`}
            >
              Blog Home 1
            </a> */}
            <a
              className={
                window.location.pathname.includes(
                  `${process.env.PUBLIC_URL}/blog-alt-home`
                )
                  ? "dropdown-item active"
                  : "dropdown-item"
              }
              href={`${process.env.PUBLIC_URL}/blog-alt-home`}
            >
              Blog Home
            </a>
            <a
              className={
                window.location.pathname.includes(
                  `${process.env.PUBLIC_URL}/blog-post`
                )
                  ? "dropdown-item active"
                  : "dropdown-item"
              }
              href={`${process.env.PUBLIC_URL}/blog-post`}
            >
              Blog Post
            </a>
          </div>
          {/* </MenuItem> */}
        </li>
      </ul>

      <div className="my-2">
        <a
          className="btn btn-primary"
          href="contact"
          target="_blank"
          rel="noopener noreferrer"
        >
          <i className="fas fa-paper-plane mr-2"></i>Hire Me
        </a>
      </div>

      <div className="dark-mode-toggle text-center w-100">
        <hr className="mb-4" />
        <h4 className="toggle-name mb-3 ">
          <i className="fas fa-adjust mr-1"></i>Dark Mode
        </h4>

        <input className="toggle" id="darkmode" type="checkbox" />
        <label className="toggle-btn mx-auto mb-0" htmlFor="darkmode"></label>
      </div>
      {/* <!--//dark-mode-toggle--> */}
    </div>
  );
};
