import React from "react";
import { v4 as uuid } from "uuid";

export const MenuItem: React.FC<{
         menuLink: string;
         text: string;
         iconClass?: string;
       }> = ({ menuLink, text, iconClass }) => {
         return (
           <li
             key={text}
             className={
               window.location.pathname.includes(`/${menuLink}`)
                 ? "nav-item active"
                 : "nav-item"
             }
           >
             <a
               className="nav-link"
               href={`${process.env.PUBLIC_URL}/${menuLink}`}
             >
               <i className={iconClass}></i>
               {text}
               <span className="sr-only">(current)</span>
             </a>
           </li>
         );
       };
