import React from "react";
import Disqus from "disqus-react";
interface IDisqusConfiguration {
  url: string;
  identifier: string;
  title: string;
}

export const ArticleCommentCount: React.FC<IDisqusConfiguration> = ({
  url,
  identifier,
  title,
}) => {
  return (
    <Disqus.CommentCount
      shortname="codingpro-1"
      config={{
        url: url,
        identifier: identifier,
        title: title,
      }}
    >
      {/* Placeholder Text */}
      Comments
    </Disqus.CommentCount>
  );
};
