import React from "react";

interface IProps {
  id: string;
  src: string;
  href?: string;
  captionText: string;
  captionHref?: string;
}

export const Banner: React.FC<IProps> = ({
  id,
  src,
  href,
  captionText,
  captionHref,
}) => {
  return (
    <figure id={id} className="blog-banner">
      <a href={href}>
        <img className="img-fluid" src={src} alt={`for ${src}`} />
      </a>
      <figcaption className="mt-2 text-center image-caption">
        Image Credit:{" "}
        <a href={captionHref} target="_blank" rel="noopener noreferrer">
          {captionText}
        </a>
      </figcaption>
    </figure>
  );
};
