import React, { Fragment } from "react";
import { ArticleCommentCount } from "./ArticleCommentCount";
import { formatDistanceToNow } from "date-fns";

interface IProps {
  title: string;
  identifier?: string;
  type?: string;
  published: string;
  time: string;
}
export const ArticleHeader: React.FC<IProps> = ({
  title,
  type,
  identifier,
  published,
  time,
}) => {
  return (
    <header className="blog-post-header">
      <h3 className="title mb-1">
        {identifier ? (
          <a href={`${process.env.PUBLIC_URL}/post/${type}/${identifier}`}>
            {title}
          </a>
        ) : (
          <Fragment>{title}</Fragment>
        )}
      </h3>
      <div className="meta mb-1">
        <span className="date">
          {`Published ${formatDistanceToNow(new Date(published), {
            addSuffix: true,
          })}`}
        </span>
        <span className="time">{time}</span>
        <span className="comment">
          {/* <a href="#"> */}
          <ArticleCommentCount
            url={window.location.href}
            identifier={title}
            title={title}
          />
          {/* </a> */}
        </span>
      </div>
    </header>
  );
};
