import React, { Fragment } from "react";

export const Contact = () => {
  return (
    <Fragment>
      <section className="cta-section theme-bg-light py-5">
        <div className="container text-center single-col-max-width">
          <h2 className="heading">Contact</h2>
          <div className="intro">
            <p>
              Interested in hiring me for your project or just want to say hi?
              You can fill in the contact form below or send me an email to{" "}
              <a href="mailto:#">manishki@live.com</a>
            </p>
            <p>
              Want to get connected? Follow me on the social channels below.
            </p>
            <ul className="list-inline mb-0">
              <li className="list-inline-item mb-3">
                <a
                  className="twitter"
                  href="https://twitter.com/manish_bhr"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-twitter fa-fw fa-lg"></i>
                </a>
              </li>

              <li className="list-inline-item mb-3">
                <a
                  className="linkedin"
                  href="https://www.linkedin.com/in/manish-kumar-choudhary/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-linkedin-in fa-fw fa-lg"></i>
                </a>
              </li>
              <li className="list-inline-item mb-3">
                <a
                  className="github"
                  href="https://github.com/manishki"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-github-alt fa-fw fa-lg"></i>
                </a>
              </li>
              <li className="list-inline-item">
                <a
                  className="instagram"
                  href="https://www.instagram.com/immkch/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-instagram fa-fw fa-lg"></i>
                </a>
              </li>
              <li className="list-inline-item mb-3">
                <a
                  className="stack-overflow"
                  href="https://stackoverflow.com/users/4523305/manish-kumar-choudhary"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-stack-overflow fa-fw fa-lg"></i>
                </a>
              </li>
              <li className="list-inline-item mb-3">
                <a
                  className="medium"
                  href="https://medium.com/@manishki007"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-medium-m fa-fw fa-lg"></i>
                </a>
              </li>
              <li className="list-inline-item mb-3">
                <a
                  className="codepen"
                  href="https://codepen.io/manishki"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-codepen fa-fw fa-lg"></i>
                </a>
              </li>

              <li className="list-inline-item mb-3">
                <a
                  className="facebook"
                  href="https://www.facebook.com/Mrmanishki"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <i className="fab fa-facebook-f fa-fw fa-lg"></i>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </section>
      <section className="contact-section px-3 py-5 p-md-5">
        <div className="container">
          <form
            id="contact-form"
            className="gform contact-form col-lg-8 mx-lg-auto"
            method="post"
            action="https://script.google.com/macros/s/AKfycbzqcVTxnEr-WP2X3EYpGWbQ7VC_DEipQII1bt9j/exec"
          >
            <h3 className="text-center mb-3">Get In Touch</h3>

            <div className="form-row">
              <div className="form-group col-md-6">
                <label className="sr-only" htmlFor="cname">
                  Name
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="cname"
                  name="name"
                  placeholder="Name"
                  required
                  aria-required="true"
                />
              </div>
              <div className="form-group col-md-6">
                <label className="sr-only" htmlFor="cemail">
                  Email
                </label>
                <input
                  type="email"
                  className="form-control"
                  id="cemail"
                  name="email"
                  placeholder="Email"
                  required
                  aria-required="true"
                />
              </div>
              <div className="form-group col-12">
                <select id="services" className="custom-select" name="services">
                  <option>Choose inquiry...</option>
                  <option value="basic">General inquiry</option>
                  <option value="standard">
                    Technical Support and security issues
                  </option>
                  <option value="premium">Sales inquiry</option>
                  <option value="not sure">Not sure</option>
                </select>
                {/* <small className="form-text text-muted pt-1">
                  <i className="fas fa-info-circle mr-2 text-primary"></i>Want
                  to know what's included in each package? Check the{" "}
                  <a href="services" target="_blank" rel="noopener noreferrer">
                    Services &amp; Pricing
                  </a>{" "}
                  page.
                </small> */}
              </div>
              <div className="form-group col-12">
                <label className="sr-only" htmlFor="cmessage">
                  Your message
                </label>
                <textarea
                  className="form-control"
                  id="cmessage"
                  name="message"
                  placeholder="Enter your message"
                  rows={10}
                  required
                  aria-required="true"
                ></textarea>
              </div>
              <div className="form-group col-12">
                <button
                  type="submit"
                  className="btn btn-block btn-primary py-2"
                >
                  Send Now
                </button>
              </div>
            </div>
            <div
              style={{ display: "None" }}
              className="thankyou_message text-muted pt-1"
            >
              <p>
                <i className="fas fa-info-circle mr-2 text-primary"></i>
                <em>Thanks</em> for contacting us! We will get back to you soon!
              </p>
            </div>
          </form>
        </div>
      </section>
    </Fragment>
  );
};
